<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card style="margin: 10px 0">
      <p style="text-align: left; margin: 10px 0">
        <el-button type="primary" @click="dialogFormVisible = true"
          ></el-button
        >
      </p>
      <el-table :data="rolesData" style="width: 100%">
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="item1 in scope.row.children" :key="item1.id">
              <!--  -->
              <el-col :span="5">
                <el-tag>{{ item1.authName }}</el-tag>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row v-for="item2 in item1.children" :key="item2.id">
                  <el-col :span="6"
                    ><el-tag type="success">{{
                      item2.authName
                    }}</el-tag></el-col
                  >
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="#" type="index"> </el-table-column>
        <el-table-column label="" prop="roleName"> </el-table-column>
        <el-table-column label="" prop="roleDesc"> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="edit(scope.row.id)"></el-button>
            <el-button size="mini" type="danger" @click="del(scope.row.id)"
              ></el-button
            >
            <el-button
              size="mini"
              type="warning"
              @click="showSetRole(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form
          :label-position="labelPosition"
          label-width="auto"
          :model="rolesForm"
          :rules="rolesFormRules"
          ref="rolesFormRef"
          class="formBody"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="rolesForm.roleName"> </el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="rolesForm.roleDesc"> </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="addRoles"> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogEditVisible">
        <el-form
          :label-position="labelPosition"
          label-width="auto"
          :model="rolesEditForm"
          :rules="rolesFormRules"
          ref="rolesEditFormRef"
          class="formBody"
        >
          <el-form-item label="" prop="roleName">
            <el-input v-model="rolesEditForm.roleName"> </el-input>
          </el-form-item>
          <el-form-item label="" prop="roleDesc">
            <el-input v-model="rolesEditForm.roleDesc"> </el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogEditVisible = false"> </el-button>
          <el-button type="primary" @click="editRoles"> </el-button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog title="" :visible.sync="dialogRolesVisible">
        <el-tree
          :data="rightsList"
          show-checkbox
          node-key="id"
          :default-checked-keys="defKeys"
          :props="rightsProps"
          ref="tree"
          default-expand-all
        >
        </el-tree>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogRolesVisible = false"> </el-button>
          <el-button type="primary" @click="allRights"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rolesData: [],
      dialogFormVisible: false,
      dialogEditVisible: false,
      dialogRolesVisible: false,
      labelPosition: "right",
      rolesForm: {
        roleName: "",
        roleDesc: "",
      },
      rolesEditForm: {},
      rolesFormRules: {
        roleName: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        roleDesc: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
      },
      rightsList: [],
      // 
      rightsProps: {
        children: "children",
        label: "authName",
      },
      roleId: "",
      // 
      defKeys: [],
    };
  },
  created() {
    this.xr();
  },
  methods: {
    xr() {
      this.$axios.get("roles").then((res) => {
        // console.log(res);
        this.rolesData = res.data.data;
      });
    },
    addRoles() {
      this.$refs.rolesFormRef.validate((val) => {
        if (!val) return;
        this.$axios.post("roles", this.rolesForm).then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 201) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          // 
          this.dialogFormVisible = false;
          // 
          this.xr();
        });
      });
    },
    del(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$axios.delete(`roles/${id}`).then((res) => {
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            this.xr();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    edit(id) {
      // console.log(id);
      this.$axios.get(`roles/${id}`).then((res) => {
        console.log(res);
        this.rolesEditForm = res.data.data;
        this.dialogEditVisible = true;
      });
    },
    // 
    editRoles() {
      // console.log(this.rolesEditForm);
      this.$refs.rolesEditFormRef.validate((val) => {
        if (!val) return;
        this.$axios
          .put(`roles/${this.rolesEditForm.roleId}`, this.rolesEditForm)
          .then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            this.xr();
            // 
            this.dialogEditVisible = false;
          });
      });
    },
    //  -  - 
    showSetRole(row) {
      this.defKeys = [];
      this.roleId = row.id;
      this.dialogRolesVisible = true;
      this.$axios.get("rights/tree").then((res) => {
        // console.log(res);
        this.rightsList = res.data.data;
      });
      /* 
        
          row
          this.defKeys
      */
      this.getLeafKeys(row, this.defKeys);
    },
    // id
    getLeafKeys(obj, arr) {
      //id
      if (!obj.children) {
        return arr.push(obj.id);
      }
      //  children 
      obj.children.forEach((item) => this.getLeafKeys(item, arr));
    },
    // 
    allRights() {
      
      let arrStr = this.$refs.tree
        .getCheckedKeys()
        .concat(this.$refs.tree.getHalfCheckedKeys())
        .join(",");
      // console.log(this.roleId,arrStr);
      this.$axios
        .post(`roles/${this.roleId}/rights`, { rids: arrStr })
        .then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 200) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          this.xr();
          this.dialogRolesVisible = false;
        });
    },
  },
};
</script>
<style>
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-row {
  display: flex;
  align-items: center;
  /* border-top: 1px solid #ebeef5; */
  /* box-sizing: border-box; */
}
.el-row .el-tag {
  margin: 10px;
}
.formBody {
  padding: 5px 20px;
}
</style>
