<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="margin: 10px 0">
      <!--  -->
      <div class="box">
        <el-button type="primary" @click="openDialog"></el-button>
      </div>
      <!--  -->
      <tree-table
        :data="cateCheckList"
        :columns="columns"
        border
        show-index
        :selection-type="false"
        class="treeTable"
        :expand-type="false"
        index-text="#"
      >
        <template slot="isOk" slot-scope="scope">
          <i
            class="el-icon-circle-check"
            v-if="scope.row.cat_deleted === false"
          ></i>
          <i class="el-icon-circle-close" v-else></i>
        </template>
        <template slot="isOld" slot-scope="scope">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag type="success" v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag type="warning" v-else></el-tag>
        </template>
        <template slot="opt" slot-scope="scope">
          <el-button
            size="mini"
            icon="el-icon-edit"
            type="primary"
            @click="edit(scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            icon="el-icon-delete"
            type="danger"
            @click="del(scope.row.cat_id)"
            ></el-button
          >
        </template>
      </tree-table>
      <!--  -->
      <div class="box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 3, 5, 7]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogCateVisible">
      <el-form
        ref="add"
        label-width="auto"
        style="text-align: left"
        :rules="rules"
        :model="addForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addForm.cat_name"> </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            @change="parentCate"
            :options="CateList"
            :props="cateProps"
            clearable
            style="width: 100%"
            v-model="selectedKeys"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCateVisible = false"> </el-button>
        <el-button type="primary" @click="add"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogEditVisible">
      <el-form
        ref="edit"
        label-width="auto"
        style="text-align: left"
        :rules="rules"
        :model="editForm"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editForm.cat_name"> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEditVisible = false"> </el-button>
        <el-button type="primary" @click="editCate"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        pagenum: 1, // 
        pagesize: 5, // 
      },
      // 
      total: 0,
      // 
      cateCheckList: [],
      // 
      selectedKeys: [],
      // 
      addForm: {
        cat_pid: 0,
        cat_name: "",
        cat_level: 0,
      },
      editForm: {},
      CateList: [],
      cateProps: {
        value: "cat_id", //ID
        label: "cat_name", //
        children: "children", //
        checkStrictly: true, //
        expandTrigger: "hover", //
      },
      // 
      dialogCateVisible: false,
      dialogEditVisible: false,
      // 
      columns: [
        {
          label: "", //
          prop: "cat_name", //
        },
        {
          label: "",
          prop: "cat_deleted",
          type: "template", //html
          template: "isOk", //html
        },
        {
          label: "",
          prop: "cat_level",
          type: "template",
          template: "isOld",
        },
        {
          label: "",
          type: "template",
          template: "opt",
        },
      ],
      // 
      rules: {
        cat_name: [
          {
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
      },
    };
  },
  created() {
    this.xr();
  },
  methods: {
    // 
    xr() {
      this.$axios.get("categories", { params: this.queryInfo }).then((res) => {
        // console.log(res);
        // if (res.data.meta.status !== 200)
        //   return this.$message.error(res.data.meta.msg);
        // this.$message.success(res.data.meta.msg);
        this.cateCheckList = res.data.data.result;
        this.total = res.data.data.total;
      });
    },
    // 
    openDialog() {
      this.$axios.get("categories", { type: 2 }).then((res) => {
        // console.log(res);
        this.dialogCateVisible = true;
        this.CateList = res.data.data;
      });
    },
    // 
    parentCate() {
      if (this.selectedKeys.length === 0) {
        this.addForm.cat_pid = 0;
        this.addForm.cat_level = 0;
      } else {
        this.addForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1];
        this.addForm.cat_level = this.selectedKeys.length;
      }
    },
    // 
    add() {
      this.$refs.add.validate((val) => {
        if (!val) return;
        this.$axios.post("categories", this.addForm).then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 201)
            return this.$message.error(res.data.meta.msg);
          this.$message.success(res.data.meta.msg);
          this.dialogCateVisible = false;
          this.xr();
          this.clear();
        });
      });
    },
    // 
    del(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$axios.delete(`categories/${id}`).then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200)
              return this.$message.error(res.data.meta.msg);
            this.$message.success(res.data.meta.msg);
            this.dialogCateVisible = false;
            this.xr();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    edit(row) {
      this.editForm = row;
      this.dialogEditVisible = true;
    },
    // 
    editCate() {
      this.$refs.edit.validate((val) => {
        if (!val) return;
        this.$axios
          .put(`categories/${this.editForm.cat_id}`, this.editForm)
          .then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200)
              return this.$message.error(res.data.meta.msg);
            this.$message.success(res.data.meta.msg);
            this.dialogEditVisible = false;
            this.xr();
          });
      });
    },
    // 
    clear() {
      (this.addForm = {
        cat_pid: 0,
        cat_name: "",
        cat_level: 0,
      }),
        (this.selectedKeys = []);
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.xr();
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.xr();
      // console.log(`: ${val}`);
    },
  },
};
</script>
