<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card style="margin: 10px 0">
      <!--  -->
      <div class="box">
        <el-input
          placeholder=""
          v-model="queryInfo.query"
          clearable
          @clear="search"
          style="width: 50%; margin-right: 20px"
        >
          <template slot="append">
            <el-button @click="search" class="el-icon-search"></el-button>
          </template>
        </el-input>
        <el-button type="primary" @click="addGoodsDialogVisible = true"
          ></el-button
        >
      </div>
      <!--  -->
      <el-table :data="goodsList" border stripe>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label="" min-width="300">
        </el-table-column>
        <el-table-column prop="goods_price" label="()">
        </el-table-column>
        <el-table-column prop="goods_weight" label="">
        </el-table-column>
        <el-table-column prop="add_time" label="" width="160px">
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="150">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit(scope.row)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              @click="delGoods(scope.row.goods_id)"
              icon="el-icon-delete"
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 10, 20, 30, 50]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addGoodsDialogVisible">
      <el-form
        label-position="right"
        label-width="auto"
        :model="addForm"
        :rules="rules"
        ref="formRef"
        class="formBody"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="addForm.goods_name"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="addForm.goods_price"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="addForm.goods_number"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="addForm.goods_weight"> </el-input>
        </el-form-item>
        <el-form-item label="">
          <div style="text-align: left">
            <el-upload
              class="upload-demo"
              :action="uploadUrl"
              :headers="headersObj"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :on-success="handleSuccess"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
          </div>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="addGoodsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addGoods"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editGoodsDialogVisible">
      <el-form
        label-position="right"
        label-width="auto"
        :model="editForm"
        :rules="rules"
        ref="formRef"
        class="formBody"
      >
        <el-form-item label="" prop="goods_name">
          <el-input v-model="editForm.goods_name"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_price">
          <el-input v-model="editForm.goods_price"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_number">
          <el-input v-model="editForm.goods_number"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="goods_weight">
          <el-input v-model="editForm.goods_weight"> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editGoodsDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editGoods"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="previewVisible">
      <el-form label-position="right">
        <el-form-item label="">
          <div class="box">
            <img :src="previewPath" style="width: 100%" />
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="previewVisible = false"> </el-button>
        <el-button type="primary" @click="previewVisible = false"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        pagenum: 1, // 
        pagesize: 10, // 
      },
      // 
      total: 0,
      // 
      goodsList: [],
      // 
      addForm: {
        goods_name: "",
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        pics: [],
      },
      // 
      uploadUrl: "http://shiyansong.cn:8888/api/private/v1/upload",
      // 
      headersObj: {
        Authorization: window.sessionStorage.getItem("token"),
      },
      // 
      editForm: {},
      // 
      addGoodsDialogVisible: false,
      editGoodsDialogVisible: false,
      previewVisible: false,
      // 
      previewPath: "",
      // 
      rules: {
        goods_name: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        goods_price: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        goods_number: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        goods_weight: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },
  methods: {
    // 
    getGoods() {
      this.$axios.get("goods", { params: this.queryInfo }).then((res) => {
        // console.log(res);
        if (res.data.meta.status !== 200) {
          this.$message.error(res.data.meta.msg);
          return;
        }
        this.$message.success(res.data.meta.msg);
        this.goodsList = res.data.data.goods;
        this.total = res.data.data.total;
      });
    },
    // 
    addGoods() {
      this.$refs.formRef.validate((val) => {
        if (!val) return;
        this.$axios.post("goods", this.addForm).then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 201) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          // 
          this.addGoodsDialogVisible = false;
          // 
          this.getGoods();
          this.clear();
        });
      });
    },
    // 
    delGoods(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$axios.delete(`goods/${id}`).then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            // 
            this.getGoods();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    edit(row) {
      this.editForm = row;
      this.editGoodsDialogVisible = true;
    },
    // 
    editGoods() {
      this.$refs.formRef.validate((val) => {
        if (!val) return;
        this.$axios
          .put(`goods/${this.editForm.goods_id}`, this.editForm)
          .then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            // 
            this.editGoodsDialogVisible = false;
            // 
            this.getGoods();
          });
      });
    },
    // 
    search() {
      this.queryInfo.pagenum = 1;
      this.getGoods();
    },
    // 
    clear() {
      this.addForm = {
        goods_name: "",
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        pics: {},
      };
    },
    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.getGoods();
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.getGoods();
      // console.log(`: ${val}`);
    },
    // 
    handlePreview(file) {
      // console.log(file);
      this.previewPath = file.url;
      this.previewVisible = true;
    },
    // 
    handleRemove(file) {
      // 
      const filePath = file.response.data.tmp_path;
      // pics
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath);
      //splice
      this.addForm.pics.splice(i, 1);
      // console.log(this.addForm);
    },
    // 
    handleSuccess(response) {
      // console.log(response);
      // 
      const picInfo = { pic: response.data.tmp_path };
      // pushpics
      this.addForm.pics.push(picInfo);
    },
  },
  created() {
    this.getGoods();
  },
};
</script>
