<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card style="margin: 10px 0">
      <el-table
        :data="rightsData"
        border
        style="width: 100% min-height: 100%;"
        stripe
      >
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="authName" label=""> </el-table-column>
        <el-table-column prop="path" label=""> </el-table-column>
        <el-table-column prop="level" label=""> 
          <template slot-scope="scope">
            <el-tag v-if="scope.row.level == 0"></el-tag>
            <el-tag type="success" v-if="scope.row.level == 1"></el-tag>
            <el-tag type="warning" v-if="scope.row.level == 2"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      rightsData: [],
    };
  },
  created() {
    this.$axios.get('rights/list').then(res=>{
      // console.log(res);
      this.rightsData = res.data.data;
    })
  },
};
</script>
