<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card style="margin: 10px 0">
      <div class="box">
        <el-input
          placeholder=""
          v-model="queryInfo.query"
          clearable
          @clear="search"
          style="width: 50%; margin-right: 20px"
        >
          <template slot="append">
            <el-button @click="search" class="el-icon-search"></el-button>
          </template>
        </el-input>
        <el-button type="primary" @click="dialogFormVisible = true"
          ></el-button
        >
      </div>
      <!--  -->
      <el-table :data="userList" min-height="auto">
        <el-table-column type="index" label=""> </el-table-column>
        <el-table-column prop="username" label=""> </el-table-column>
        <el-table-column prop="email" label=""> </el-table-column>
        <el-table-column prop="mobile" label=""> </el-table-column>
        <el-table-column prop="role_name" label=""> </el-table-column>
        <el-table-column prop="mg_state" label="">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="changeState(scope.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width=200>
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="edit(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              type="danger"
              @click="delUser(scope.row.id)"
              icon="el-icon-delete"
              size="mini"
            ></el-button>
            <!--  -->
            <el-button
              type="warning"
              @click="setRoles(scope.row)"
              icon="el-icon-setting"
              size="mini"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <div class="box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[1, 2, 3, 5]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form
        :label-position="labelPosition"
        label-width="auto"
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        class="formBody"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile" maxlength="11" show-word-limit>
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogEditVisible">
      <el-form
        :label-position="labelPosition"
        label-width="auto"
        :model="editForm"
        :rules="addFormRules"
        ref="editFormRef"
        class="formBody"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="editForm.username" disabled> </el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"> </el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile" maxlength="11" show-word-limit>
          </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEditVisible = false"> </el-button>
        <el-button type="primary" @click="editUser"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogRolesVisible">
      <el-form ref="form" label-width="auto" style="text-align: left">
        <p>{{ rolesForm.username }}</p>
        <p>{{ rolesForm.role_name }}</p>
        <p>
          
          <el-select v-model="rolesValue" placeholder="">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </p>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogRolesVisible = false"> </el-button>
        <el-button type="primary" @click="setRolesInfo"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    // 
    var checkEmail = (rule, value, callback) => {
      /* 
      rule 
      value 
      callback 
      */
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    // 
    var checkMobile = (rule, value, callback) => {
      var reg = /^1[3|4|5|7|8|9][0-9]\d{8}$/;
      if (reg.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      // 
      queryInfo: {
        query: "", // 
        pagenum: 1, // 
        pagesize: 3, // 
      },
      total: 0, // 
      // 
      userList: [],
      // 
      dialogFormVisible: false,
      // 
      dialogEditVisible: false,
      dialogRolesVisible: false,
      labelPosition: "right",
      // 
      addForm: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      // 
      editForm: {},
      rolesForm: {},
      rolesList: [],
      rolesValue: "",
      // 
      addFormRules: {
        username: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        password: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
        ],
        email: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
          { validator: checkEmail, trigger: ["blur", "change"] },
        ],
        mobile: [
          {
            required: true,
            message: "",
            trigger: ["blur", "change"],
          },
          { validator: checkMobile, trigger: ["blur", "change"] },
        ],
      },
    };
  },
  created() {
    this.xr();
  },
  methods: {
    // 
    xr() {
      this.$axios
        .get("users/", {
          params: this.queryInfo,
        })
        .then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 200) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.userList = res.data.data.users;
          this.total = res.data.data.total;
        });
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate((val) => {
        if (!val) return;
        this.$axios.post("users", this.addForm).then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 201) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          // 
          this.dialogFormVisible = false;
          // 
          this.xr();
        });
      });
    },
    // 
    delUser(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$axios.delete(`users/${id}`).then((res) => {
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            this.xr();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    edit(id) {
      this.$axios.get(`users/${id}`).then((res) => {
        // console.log(res);
        this.editForm = res.data.data;
        this.dialogEditVisible = true;
      });
    },
    // 
    editUser() {
      // 
      this.$refs.editFormRef.validate((val) => {
        if (!val) return;
        this.$axios
          .put(`users/${this.editForm.id}`, this.editForm)
          .then((res) => {
            // console.log(res);
            if (res.data.meta.status !== 200) {
              this.$message.error(res.data.meta.msg);
              return;
            }
            this.$message.success(res.data.meta.msg);
            this.xr();
            // 
            this.dialogEditVisible = false;
          });
      });
    },
    //
    changeState(state) {
      this.$axios
        .put(
          `/users/${state.id}/state/${state.mg_state}`
        )
        .then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 200) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          this.xr();
        });
    },
    // 
    search() {
      this.queryInfo.pagenum = 1;
      this.xr();
    },
    //   
    setRoles(row) {
      // console.log(row);
      this.rolesForm = row;
      this.rolesValue = ''
      // 
      this.$axios.get("roles").then((res) => {
        // console.log(res);
        this.rolesList = res.data.data;
        // console.log(this.rolesList);
      });
      this.dialogRolesVisible = true;
    },
    // 
    setRolesInfo() {
      this.$axios
        .put(`users/${this.rolesForm.id}/role`, { rid: this.rolesValue })
        .then((res) => {
          // console.log(res);
          if (res.data.meta.status !== 200) {
            this.$message.error(res.data.meta.msg);
            return;
          }
          this.$message.success(res.data.meta.msg);
          this.xr();
          this.dialogRolesVisible = false;
        });
    },

    handleSizeChange(val) {
      this.queryInfo.pagesize = val;
      this.xr();
      // console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      this.queryInfo.pagenum = val;
      this.xr();
      // console.log(`: ${val}`);
    },
  },
};
</script>
<style>
.formBody {
  padding: 5px 20px;
}
</style>
